.our-services {
  overflow: hidden;
  &__intro {
    background-image: url('../img/pages/our-services-image.jpg');
    @include cover;
    height: 440px;
    @include center;
    margin-bottom: 185px;
    position: relative;
    @include r(767) {
      height: 250px;
      margin-bottom: 50px; }
    .container {
      position: relative;
      height: 100%; }
    h1 {
      background-image: linear-gradient(150deg, #9f9cef, #3d30f3 99%), linear-gradient(to bottom, #7e75bd, #7e75bd);
      font-size: 100px;
      font-weight: bold;
      line-height: 1;
      color: #7e75bd;
      -webkit-background-clip: text;
      background-clip: text;
      -webkit-text-fill-color: transparent;
      position: absolute;
      bottom: 30%;
      left: 3px !important;
      right: auto;
      display: block;
      text-align: center;
      text-transform: uppercase;
      // +r(1150)
      //   font-size: 100px
      @include r(767) {
        font-size: 40px !important; } } }
