 }    //       background-color: #30dffd
  &__block {
    border-radius: 10px;
    box-shadow: 0px 1px 23px 0 rgba(0, 0, 0, 0.08);
    border: solid 0 #000000;
    background-color: $white;
    margin: 0 7px 60px;
    overflow: hidden;
    display: block;
    &:hover {
      box-shadow: 0 25px 60px rgba(67,54,102,.2);
      .our-services__block__text {
        top: -40px; } }
    h2 {
      font-size: 21px;
      font-weight: normal;
      text-align: center;
      color: #12114a;
      margin-bottom: 35px;
      @include r(767) {
        margin-bottom: 20px; } }
    p {
      font-size: 14px;
      font-weight: normal;
      line-height: 1.88;
      text-align: center;
      color: #707070;
      margin-bottom: 55px; }
    i {
      font-size: 25px;
      font-weight: normal;
      font-style: normal;
      line-height: 1.2;
      text-align: center;
      color: rgba(40, 40, 40, 0.5);
      display: block; }
    path {
      transition: all 0.2s; }
    &__img {
      overflow: hidden;
      height: 225px;
      position: relative;
      img {
        width: 100%;
        height: 100%;
        @include object-fit(cover); }
      svg {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%; } }
    &__text {
      padding: 40px 35px 70px;
      position: relative;
      top: 0;
      transition: all 0.4s;
      @include r(767) {
        padding: 30px 15px 50px; } } } }
