    &__img {
      height: 100%;
      width: 100%;
      @include center;
      position: relative;
      min-height: 550px;
      svg {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        image {
          //width: 200%
 } }          //height: 108%
      @include r(1040) {
        align-items: flex-start;
        padding-top: 100px; }
      @include r(767) {
        background-position: 47% 0;
        height: 340px;
        min-height: 100%;
        align-items: center;
        padding-top: 70px !important;
        padding-bottom: 50px;
        margin-bottom: 40px; }
      &__icon {
        width: 50px;
        height: 50px;
        border-radius: 100%;
        background: $white;
        @include center;
        margin-right: 15px;
        @include r(767) {
          margin-right: 6px;
          width: 40px;
          height: 40px; }
        img {
          width: 30px; } }
      ul {
        position: relative;
        z-index: 3; }
      li {
        display: flex;
        align-items: center;
        margin-bottom: 13px;
        &:first-child {
          font-size: 30px;
          font-weight: bold;
          color: $white; }
        &:nth-child(2) {
          font-size: 14px;
          font-weight: bold;
          color: $white; }
        &:last-child {
          font-size: 14px;
          font-weight: bold;
          color: $white;
          text-transform: uppercase; } } }
    &__text {
      padding-left: 60px;
      padding-right: 50px;
      @include r(1040) {
        padding-left: 20px; }
      @include r(767) {
        padding: 0 !important;
        padding-top: 20px;
        position: relative;
        top: -10px; }
      h2 {
        font-size: 35px;
        font-weight: normal;
        color: #12114a;
        margin-bottom: 50px;
        @include r(1040) {
          font-size: 25px; }
        @include r(767) {
          margin-bottom: 15px; }

        span {
          text-transform: uppercase;
          font-size: 14px;
          font-weight: bold;
          line-height: 2.5;
          letter-spacing: 0.8px;
          color: #f20487;
          display: block; } }
      p {
        font-size: 17px;
        font-weight: normal;
        line-height: 1.94;
        color: #707070;
        margin-bottom: 70px;
        @include r(1040) {
          font-size: 15px; }
        @include r(767) {
          margin-bottom: 40px; } }
      div.editor-content-display {
        font-size: 17px;
        font-weight: normal;
        line-height: 1.94;
        color: #707070;
        margin-bottom: 80px;
        @include r(1040) {
          font-size: 15px; }
        @include r(767) {
          margin-bottom: 40px; } }
      &__btn {
        position: relative;
        display: inline-flex;
        a:last-child {
          padding: 13px 30px 10px;
          border-radius: 25px;
          border: solid 1px #4143ea;
          color: #4143ea;
          margin-left: 20px;
          &:hover {
            background-color: #4143ea;
            color: $white; } }
        a {
          font-size: 15px;
          span {
            font-size: 15px; }

          @include r(1040) {
            padding: 12px 12px 9px !important; } } } } } }
