.manage {
  background: rgb(244, 245, 249);
  display: flex;
  &__content {
    width: calc(100% - 70px);
    margin-left: 70px;
    @include r(767) {
      width: calc(100% - 40px);
      margin-left: 40px; } }








  &__title {
    display: flex;
    justify-content: space-between;
    padding-top: 35px;
    @include r(767) {
      flex-direction: column;
      align-items: center;
      padding-top: 20px; }
    h1 {
      font-size: 30px;
      font-weight: normal;
      color: $dark;
      @include r(767) {
        padding-bottom: 15px;
        font-size: 25px; } }
    a {
      @include r(767) {
        margin-bottom: 15px; }
      i {
        margin-right: 12px; } } }






  &__statistics {
    padding: 30px 0;
    &__block {
      border-radius: 10px;
      box-shadow: 0px 1px 4px 0 rgba(0, 0, 0, 0.1);
      background-color: $white;
      padding: 25px 30px;
      @include r(1200) {
        padding: 25px 20px; }
      @include center-y;
      min-height: 106px;
      margin-bottom: 15px;
      &__img {
        margin-right: 25px;
        width: 43px;
        img {
          width: 100%; } }
      &__text {
        display: flex;
        flex-direction: column;
        p {
          font-size: 14px;
          font-weight: 600;
          color: $dark;
          margin-bottom: 5px; }
        span {
          font-size: 30px;
          font-weight: bold;
          color: $charcoal-grey; } } } }




  &__table {
    border-radius: 10px;
    box-shadow: 0px 1px 4px 0 rgba(0, 0, 0, 0.1);
    background-color: $white;
    margin-bottom: 50px;
    overflow-x: auto;
    position: relative;
    z-index: 2;
    &::-webkit-scrollbar-track {
      background-color: $very-light-pink; }
    &::-webkit-scrollbar-thumb {
      border-radius: 2px;
      background-image: linear-gradient(330deg, #8f2794, #3d30f3 99%); }
    &::-webkit-scrollbar {
      width: 7px;
      height: 7px; }
    @include r(767) {
      margin-bottom: 30px; }
    &__content {
      display: block;
      min-width: 1150px; }
    table, tbody, thead {
      width: 100%;
      display: block; }
    tr {
      display: flex;
      align-items: center;
      padding: 0 10px;
      border-bottom: 1px solid $very-light-pink;
      @include tr;
      &:hover {
        background: rgba($very-light-pink, 0.2); } }
    thead {
      position: sticky;
      top: 0;
      background: $white;
      z-index: 10; }
    thead tr:hover {
      background: none; }
    tbody tr:last-child {
      border-bottom: none;
      ul {
        @include r(1260) {
          top: auto;
          bottom: 100px;
          &:before {
            content: '';
            top: auto;
            bottom: -16px;
            border: 8px solid transparent;
            border-top: 8px solid $white; } } } }
    td, th {
      display: flex;
      position: relative;
      &:first-child {
        width: 4%; }
      &:nth-child(2) {
        width: 10%; }
      &:nth-child(3) {
        width: 11%; }
      &:nth-child(4) {
        width: 11%; }
      &:nth-child(5) {
        width: 9%; }
      &:nth-child(6) {
        width: 10%; }
      &:nth-child(7) {
        width: 11%; }
      &:nth-child(8) {
        width: 10%; }
      &:nth-child(9) {
        width: 11%; }
      &:nth-child(10) {
        width: 8%; }
      &:last-child {
        width: 3%; } }
    th {
      padding: 25px 9px;
      display: flex;
      align-items: center;
      ul {
        left: calc(50% + 18px);
        &:before {
          left: calc(50% + 5px); }
        @include r(1250) {
          left: calc(50% + 6px);
          &:before {
            left: calc(50% + 18px); } }
        @include r(767) {
          left: calc(50% - 5px);
          &:before {
            left: calc(50% + 31px); } } }
      img {
        width: 15px; }
      a {
        &:hover {
          i {
            color: $bright-light-blue; } } }
      i {
        font-size: 12px;
        color: $dark;
        @include tr; }
      span {
        font-size: 12px;
        font-weight: bold;
        color: $dark;
        text-transform: uppercase;
        margin-right: 10px;
        @include r(767) {
          font-size: 10px; } }
      input {
        background: none;
        @include placeholder-color($dark);
        font-size: 12px;
        font-weight: bold;
        width: 100%;
        text-transform: uppercase;
        color: rgba(19, 28, 46, 0.4);
        padding-left: 8px;
        &.is-active {
          @include placeholder-color(rgba(19, 28, 46, 0.4)); } } }
    ul {
      display: none;
      position: absolute;
      left: calc(50% - 3px);
      top: 30%;
      transform: translate(-50%, 50%);
      width: 105px;
      border-radius: 7px;
      box-shadow: 0px 1px 5px 0 rgba(0, 0, 0, 0.24);
      background-color: $white;
      z-index: 10;
      @include r(1250) {
        left: calc(50% - 15px); }
      @include r(767) {
        left: calc(50% - 25px); }
      &:before {
        content: '';
        position: absolute;
        left: calc(50% - 1px);
        transform: translate(-50%) rotate(45deg);
        background: $white;
        width: 10px;
        height: 10px;
        top: -6px;
        box-shadow: 0px 1px 5px 0 rgba(0, 0, 0, 0.24);
        z-index: 3;
        @include r(1250) {
          left: calc(50% + 12px); }
        @include r(767) {
          left: calc(50% + 27px); } }
      li {
        position: relative;
        z-index: 5;
        background: $white;
        border-radius: 10px;
        &:first-child {
          padding-top: 5px; }
        &:last-child {
          padding-bottom: 5px; } }
      a {
        padding: 2px 13px;
        display: flex;
        align-items: center;
        font-size: 12px;
        font-weight: normal;
        font-style: normal;
        font-stretch: normal;
        line-height: 2.08;
        letter-spacing: normal;
        text-align: left;
        color: $dark;
        cursor: pointer;
        &.is-active {
          font-weight: 600; }
        &:hover {
          font-weight: 600; }
        i {
          @include center;
          width: 20px;
          height: 20px;
          color: $bright-light-blue;
          border-radius: 100%;
          border: 1px solid $dark;
          font-size: 9px;
          margin-right: 10px;
          @include tr; } } }
    th.table__drop-down {
      li {
        a {
          &:before {
            content: '';
            width: 9px;
            height: 9px;
            background-color: $fresh-green;
            border-radius: 100%;
            margin-right: 7px; } }
        &:last-child {
          a {
            &:before {
              background-color: $strawberry; } } } } }
    .table__datepicker {
      position: relative;
      input {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        cursor: pointer; } }
    .table__gallery {
      display: flex;
      a {
        margin-right: 5px;
        position: relative;
        @include center;
        cursor: default;
        &:nth-child(2) {
          cursor: pointer; }
        &:nth-child(n+3) {
          display: none; }
        img {
          @include r(767) {
            width: 45px;
            height: 45px; } }
        i {
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          @include center;
          color: $white;
          background: rgba($charcoal-grey, 0.3);
          font-size: 22px;
          @include tr; }
        &:hover {
          i {
            color: $bright-light-blue;
            background: rgba($charcoal-grey, 0.5); } } } }
    .table__more {
      i {
        font-size: 24px;
        color: $dark;
        @include tr; }
      &:hover {
        i {
          color: $bright-light-blue; } } }
    td {
      padding: 25px 12px;
      text-align: left;
      position: relative;
      p {
        font-size: 14px;
        font-weight: normal;
        color: $dark;
        @include tr;
        @include r(767) {
          font-size: 11px; } }
      a:hover {
        p {
          color: $bright-light-blue; } } }
    tr.open {
      background-color: #f5fff1;
      p {
        font-weight: 600; } } }






  &__create, {
    position: fixed;
    right: -100%;
    top: 0;
    max-width: 700px;
    width: 100%;
    height: 100vh;
    background-color: #f2f2f2;
    box-shadow: -1.5px 2px 16px 0 rgba(2, 23, 56, 0.19);
    transition: all 0.9s;
    z-index: 15;
    @include r(767) {
      max-width: 100%; }
    &.is-active {
      right: 0; }
    &--bg {
      background: rgba(#e0e0e0, 0.8);
      position: fixed;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      display: none;
      z-index: 14; }
    &__header {
      width: 100%;
      padding: 33px;
      background: $white;
      border-bottom: 1px solid #e0e0e0;
      a {
        font-size: 25px;
        font-weight: 600;
        color: $dark;
        display: block;
        &:hover {
          color: $bright-light-blue;
          i {
            color: $dark; } }
        i {
          font-size: 19px;
          color: $bright-light-blue;
          margin-right: 33px;
          @include tr; } } }
    &__content {
      padding: 30px;
      overflow: scroll;
      height: calc(100vh - 172px);
      @include r(767) {
        padding: 15px 10px; } } }
  &__form {
    border-radius: 10px;
    box-shadow: 0px 1px 9px 0 rgba(0, 0, 0, 0.16);
    background-color: $white;
    padding: 40px 50px;
    margin-bottom: 30px;
    @include r(767) {
      padding: 15px 10px; }
    h4 {
      font-size: 14px;
      font-weight: 600;
      color: $charcoal-grey;
      margin-top: 50px;
      display: flex;
      align-items: center; }
    &__title {
      @include center;
      justify-content: space-between;
      span {
        width: 30px;
        height: 30px;
        border: solid 1px $dark;
        @include center;
        display: inline-flex;
        font-size: 12px;
        font-weight: bold;
        color: $charcoal-grey;
        border-radius: 100%;
        margin-right: 15px;
        @include r(767) {
          margin-right: 6px; } }
      h3 {
        font-size: 18px;
        font-weight: bold;
        color: $dark;
        @include center; }
      p {
        font-size: 12px;
        color: $orangey-red;
        display: inline-flex;
        align-items: center;
        &:before {
          content: '';
          width: 5px;
          height: 5px;
          border-radius: 100%;
          background: $orangey-red;
          display: block;
          margin-right: 5px; } } }
    .ql-editor.ql-blank::after {
      content: '';
      position: absolute;
      left: 97px;
      top: 17px;
      width: 6px;
      height: 6px;
      background-color: $orangey-red;
      border-radius: 100%; }
    .about-trainer .ql-editor.ql-blank::after {
      left: 110px; }
    &__input, &__description {
      width: 100%;
      position: relative;
      padding-top: 30px;
      &--select {
        display: flex;
        @include r(530) {
          flex-direction: column; }
        .select-country, .select-state {
          width: 100%; }
        .select-state {
          margin-left: 30px;
          @include r(530) {
            margin-left: 0; } } }
      .selectize-input {
        box-shadow: none;
        border: none;
        border-bottom: 1px solid #cecece;
        padding: 10px 0 5px;
        border-radius: 0;
        height: 36px; }
      .selectize-input::after {
        display: none !important;
        right: 0px !important; }
      &.select-gear {
        .selectize-input::after {
          display: block !important; } }
      .selectize-control.multi .selectize-input.has-items {
        padding: 10px 0 !important;
        height: auto; }
      input, textarea {
        width: 100%;
        border-bottom: 1px solid #cecece;
        font-size: 14px;
        font-weight: normal;
        color: #616161;
        padding-bottom: 10px;
        background: none;
        &.focused {
          & + label {
            opacity: 0; } }
        &:focus {
          & + label {
            opacity: 0; } }
        &:-webkit-autofill {
          & + label {
            opacity: 0; } }
        &:valid {
          & + label {
            opacity: 0; } } }
      textarea {
        height: 204px;
        border-radius: 10px;
        border: solid 1px #909090;
        background-color: #f2f2f2;
        padding: 20px 30px; }
      label {
        font-size: 14px;
        font-weight: normal;
        color: #616161;
        position: absolute;
        left: 0;
        bottom: 10px;
        @include tr;
        &:before {
          content: '';
          position: absolute;
          right: -10px;
          top: 5px;
          width: 6px;
          height: 6px;
          background-color: $orangey-red;
          border-radius: 100%; } }
      &.select-country, &.select-state {
        .point {
          position: absolute;
          left: 45px;
          z-index: 9;
          top: 45px;
          width: 6px;
          height: 6px;
          background-color: $orangey-red;
          border-radius: 100%; } }
      &.select-state {
        .point {
          left: 82px; } } }
    &__description {
      background-color: #f2f2f2;
      padding-top: 0;
      margin-top: 30px;
      border-radius: 10px;
      label {
        top: 20px;
        left: 30px;
        bottom: auto; } }
    &__fee {
      display: flex;
      margin-top: 30px;
      @include r(530) {
        flex-direction: column; }
      input {
        width: 100%;
        border-bottom: 1px solid #cecece;
        margin-right: 15px;
        padding: 9px 0;
        font-size: 14px;
        font-weight: normal;
        color: #616161;
        @include r(530) {
          margin-right: 0;
          margin-bottom: 15px; }
        &:last-child {
          margin-right: 0; } } }
    &__datepicker {
      display: flex;
      padding-top: 40px;
      flex-wrap: wrap;
      &__block {
        position: relative;
        width: calc(50% - 15px);
        @include r(530) {
          width: 100%; }
        &:last-child {
          margin-left: 30px;
          @include r(530) {
            margin: 0;
            padding-top: 20px; } } }
      input {
        width: 100%;
        border-bottom: 1px solid #cecece;
        font-size: 14px;
        font-weight: normal;
        color: #616161;
        padding-bottom: 10px;
        background: none;
        position: relative;
        z-index: 3;
        cursor: pointer;
        &.focused {
          & + label {
            opacity: 0; } }
        &:focus {
          & + label {
            opacity: 0; } }
        &:-webkit-autofill {
          & + label {
            opacity: 0; } }
        &:valid {
          & + label {
            opacity: 0; } } }
      label {
        font-size: 14px;
        font-weight: normal;
        color: #616161;
        position: absolute;
        left: 0;
        bottom: 10px;
        @include tr;
        &:before {
          content: '';
          position: absolute;
          right: -10px;
          top: 5px;
          width: 6px;
          height: 6px;
          background-color: $orangey-red;
          border-radius: 100%; } }
      a {
        position: absolute;
        right: 0;
        width: 20px;
        img {
          width: 100%; } } }
    &__image {
      @include center;
      justify-content: space-between;
      margin-top: 40px;
      @include r(530) {
        flex-wrap: wrap; }
      &__block {
        width: 110px;
        height: 110px;
        position: relative;
        border-radius: 10px;
        border: dashed 1px #a1a1a1;
        overflow: hidden;
        @include tr;
        @include r(530) {
          width: 49%; }
        &:hover {
          background: #f2f2f2; }
        span {
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          width: 100%;
          height: 100%;
          @include center;
          span {
            @include center; }
          img {
            width: 100%; } }
        @include r(530) {
          margin-bottom: 10px; }
        input {
          width: 100%;
          height: 100%;
          position: absolute;
          left: 0;
          top: 0;
          opacity: 0;
          cursor: pointer;
          z-index: 5; }
        p {
          width: 100%;
          height: 100%;
          @include center;
          background: rgba(0, 0, 0, 0.5);
          position: relative;
          z-index: 4; }
        a {
          @include center-column;
          font-size: 12px;
          font-weight: normal;
          color: $white;
          text-align: center;
          &.delete {
            position: absolute;
            width: 100%;
            height: 100%; } }
        i {
          width: 20px;
          height: 20px;
          @include center;
          background: $orangey-red;
          color: $white;
          font-size: 13px;
          border-radius: 100%;
          margin-bottom: 7px; }
        &--add {
          p {
            background: none; }
          a {
            color: #616161; }
          i {
            background: $bright-light-blue;
            font-size: 10px; } } } }
    &__btn {
      display: flex;
      margin-left: 20px;
      a {
        font-size: 12px;
        font-weight: 600;
        @include center;
        &:hover {
          text-decoration: underline; }
        i {
          width: 28px;
          height: 28px;
          border-radius: 100%;
          color: $white;
          font-size: 13px;
          @include center; } }
      &__add-more {
        color: #3cc6f2;
        margin-right: 10px;
        i {
          background: #3cc6f2; } }
      &__delete {
        color: #ed4444;
        i {
          background: #ed4444; } } }
    &__footer {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      padding: 20px 40px;
      background: $white;
      @include center-y;
      justify-content: flex-end;
      border-top: 1px solid #cecece;
      a {
        margin-right: 10px;
        border: solid 0.5px $dark;
        background: none;
        color: $dark;
        font-size: 12px;
        text-transform: uppercase;
        padding-top: 13px;
        &:before, &:after {
          display: none; }
        &:hover {
          color: $white;
          background: $dark; } }
      button {
        font-size: 12px;
        text-transform: uppercase; } } } }




.message-popup {
  position: fixed;
  right: -100%;
  top: 0;
  max-width: 700px;
  width: 100%;
  height: 100vh;
  background-color: #f2f2f2;
  box-shadow: -1.5px 2px 16px 0 rgba(2, 23, 56, 0.19);
  transition: all 0.9s;
  z-index: 15;
  @include r(767) {
    max-width: 100%; }
  &.is-active {
    right: 0; }
  &__content {
    overflow: scroll;
    height: calc(100vh - 100px); }
  &__block {
    margin: 30px;
    padding: 50px 45px 20px;
    border-radius: 10px;
    box-shadow: 0px 1px 9px 0 rgba(0, 0, 0, 0.16);
    background-color: $white;
    p {
      font-size: 14px;
      line-height: 1.79;
      color: $charcoal-grey;
      padding-bottom: 30px; } } }
.message-popup--bg {
  background: rgba(0, 0, 0, 0.4);
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: none;
  z-index: 14; }
