html {}
body {
  font-family: 'Eina03', sans-serif;
  font-size: 14px;
  box-sizing: border-box;
  position: relative; }
.container {
  width: 100% !important;
  max-width: 1160px;
  box-sizing: border-box;
  @include r(1250) {
    padding: 0 15px; } }
a {
  text-decoration: none;
  transition: all 0.3s; }
input[type="submit"] {
  appearance: none;
  -webkit-appearance: none; }
textarea {
  resize: none; }
.flex {
  display: flex; }

@include r(767) {
	.hidden-xs {
		display: none !important; } }
@media only screen and (min-width: 768px) and (max-width: 1024px) {
	.hidden-sm {
		display: none !important; } }
@include rmin(1024) {
	.hidden-md {
		display: none !important; } }


.btn {
  border-radius: 20px;
  padding: 14px 30px 12px;
  @include tr;
  display: inline-flex;
  font-size: 14px;
  font-weight: bold;
  line-height: 1;
  &-primary {
    background: $bright-light-blue;
    color: $white;
    position: relative;
    &:hover {
      &:before {
        opacity: 0; }
      &:after {
        opacity: 1; } }
    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      display: block;
      background-image: linear-gradient(330deg, #8f2794, #3d30f3 99%);
      z-index: 1;
      border-radius: 20px;
      @include tr; }
    &:after {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      display: block;
      background-image: linear-gradient(330deg, #3d30f3, #8f2794 99%);
      z-index: 1;
      opacity: 0;
      border-radius: 20px;
      @include tr; }
    span {
      display: block;
      font-size: 16px;
      display: inline-flex;
      font-size: 14px;
      font-weight: bold;
      line-height: 1;
      position: relative;
      z-index: 3; } }
  &-open {
    background-color: $fresh-green;
    border: 1px solid $fresh-green;
    color: $white;
    padding: 12px 15px 10px;
    font-size: 12px;
    text-transform: uppercase;
    &:hover {
      color: $fresh-green;
      background: none; } }
  &-closed {
    color: $strawberry;
    padding: 12px 12px 10px;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: normal; }
  &-secondary {
    border-radius: 18px;
    color: $bright-light-blue;
    border: 1px solid $bright-light-blue;
    padding: 9px 13px;
    font-weight: 600;
    &:hover {
      background: $bright-light-blue;
      color: $white; } } }



.breadcrumbs {
  @include center-y;
  margin-top: 11px;
  li {
    a {
      font-size: 12px;
      font-weight: 600;
      color: #403cef;
      position: relative;
      &:hover {
        color: $dark; }
      &:after {
        content: '/';
        font-size: 12px;
        color: $dark;
        margin: 0 5px; } } }
  li:last-child {
    a {
      font-weight: normal;
      cursor: default;
      color: $dark;
      &:hover {
        color: $dark; }
      &:after {
        display: none; } } } }

.datepicker {
  border-radius: 10px;
  box-shadow: 0px 1px 5px 0 rgba(0, 0, 0, 0.24);
  background-color: $white;
  border: none;
  width: 315px;
  @include r(767) {
    width: 280px; } }
.datepicker--pointer {
  border: none;
  top: -5px;
  left: 55px !important;
  box-shadow: 0px 1px 5px 0 rgba(0, 0, 0, 0.24);
  z-index: 1; }
.datepicker--nav-title, .datepicker--day-name {
  color: #34c9f6; }
.datepicker--nav-action path {
  stroke: #34c9f6; }
.datepicker--nav {
  border: none;
  position: relative;
  z-index: 2;
  border-radius: 10px;
  background: $white; }
.datepicker--cell.-selected- {
  border-radius: 100%; }
.datepicker--cell-day {
  width: 22px;
  margin: 12px 10.9px;
  height: 22px;
  font-size: 12px;
  @include r(767) {
    margin: 7px 8.4px; }
  &:hover {
    background: #34c9f6;
    color: $white; } }





.pages {
  padding-top: 30px;
  h1 {
    color: $bright-light-blue;
    font-size: 30px;
    padding-bottom: 30px;
    text-transform: uppercase; }
  h2 {
    color: $bright-light-blue;
    font-size: 20px;
    padding: 50px 0 0px;
    text-transform: uppercase; }
  ul {
    padding-top: 20px;
    a {
      font-size: 18px;
      padding: 5px 0;
      color: $charcoal-grey;
      display: block;
      &:hover {
        color: $bright-light-blue; } } } }
