.about {
  overflow: hidden;
  &__intro {
    background-image: url('../img/about/about__images.jpg');
    @include cover;
    height: 440px;
    position: relative;
    @include center;
    @include r(767) {
      height: 250px; }
    .container {
      position: relative;
      height: 100%; }
    h1 {
      background-image: linear-gradient(150deg, #9f9cef, #3d30f3 99%), linear-gradient(to bottom, #7e75bd, #7e75bd);
      font-size: 100px;
      font-weight: bold;
      line-height: 1;
      color: #7e75bd;
      -webkit-background-clip: text;
      background-clip: text;
      -webkit-text-fill-color: transparent;
      position: absolute;
      bottom: 30%;
      left: 3px !important;
      right: auto;
      display: block;
      text-align: center;
      text-transform: uppercase;
      padding-left: 20px;
      @include r(767) {
        font-size: 50px; } } }
  &__journey {
    padding-top: 125px;
    @include r(767) {
      padding-top: 40px; }
    // &__decor
    //   position: absolute
    //   bottom: 25%
    //   right: -60px
    //   width: 24px
    //   height: 24px
    //   border-radius: 100%
    //   display: block
    //   border: 5px solid rgb(125, 206, 160)
    &__content {
      position: relative;
      display: flex;
      justify-content: flex-end;
      @include r(767) {
        flex-direction: column; } }
    &__title {
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      max-width: 465px;
      min-height: 445px;
      width: 100%;
      background: $white;
      border-radius: 13.2px;
      box-shadow: -1.5px 12.4px 48.5px 0 rgba(0, 0, 0, 0.17);
      padding-top: 30px;
      background-image: url('../img/about/journey.jpg');
      @include cover;
      @include r(1160) {
        position: static;
        transform: none;
        margin-right: 10px; }
      @include r(1040) {
        padding: 40px; }
      @include r(767) {
        max-width: 100%;
        margin-left: 0;
        padding: 20px; }
      h2 {
        text-align: center;
        font-size: 25px;
        font-weight: bold;
        color: $white;
        line-height: 1;
        margin-bottom: 40px;
        text-transform: uppercase; }
      img {
        width: 100%; } }
    &__text {
      max-width: 925px;
      width: 100%;
      min-height: 570px;
      padding: 50px 70px;
      border-radius: 13.2px;
      border: 1px solid #12114a;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      justify-content: center;
      @include r(1160) {
        margin-left: 10px; }
      @include r(1040) {
        padding: 25px; }
      @include r(767) {
        margin-left: 0;
        margin-top: 20px;
        padding: 20px;
        min-height: 100%; }
      p {
        padding-left: 32%;
        width: 100%;
        font-size: 14px;
        font-weight: normal;
        line-height: 2.2;
        color: #12114a;
        padding-bottom: 40px;
        display: block;
        @include r(1160) {
          padding-left: 0; }
        @include r(767) {
          line-height: 1.6;
          padding-bottom: 20px; }
        &:last-child {
          padding-bottom: 0; } } } }
  &__advantages {
    padding: 120px 0;
    position: relative;
    @include r(1040) {
      padding: 60px 0 40px; }
    // &__decor
    //   +r(500)
    //     display: none
    //   img:first-child
    //     position: absolute
    //     left: 40px
    //     top: 60px
    //     width: 170px
    //     +r(1040)
    //       width: 150px
    //       top: 100px
    //   img:last-child
    //     position: absolute
    //     right: 40px
    //     bottom: 80px
    //     width: 60px
    &__block {
      @include center;
      align-items: flex-start;
      @include r(1040) {
        padding-bottom: 30px; }
      &__img {
        margin-right: 31px;
        padding-top: 10px;
        width: 80px;
        @include center;
        justify-content: flex-end;
        img {
          height: 53px; } }
      &__text {
        max-width: 110px;
        span {
          font-size: 44px;
          display: block;
          margin-bottom: 10px; }
        p {
          font-size: 15.8px;
          line-height: 1.56;
          color: #12114a; } } } }
  &__vision {
    padding: 110px 0;
    background-color: #f8f8fa;
    @include r(767) {
      padding: 40px 0; }
    h2 {
      font-size: 35px;
      font-weight: normal;
      color: #12114a;
      margin-bottom: 60px;
      @include r(767) {
        margin-bottom: 30px; } }
    p {
      font-size: 14px;
      font-weight: normal;
      line-height: 1.75;
      color: #707070;
      margin-bottom: 30px; }
    &__img {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      height: 100%;
      @include r(767) {
        justify-content: center; }
      img {
        max-width: 463px;
        width: 100%; } } }
  &__committee {
    padding: 0px 0 110px;
    position: relative;
    @include r(767) {
      padding: 0px 0 40px; }
    .container {
      position: relative; }
    &__parallax {
      position: absolute;
      right: 13%;
      top: -120px;
      font-size: 175px;
      font-weight: bold;
      color: rgba(18, 17, 74, 0.03);
      text-transform: uppercase;
      @include r(1040) {
        font-size: 120px; }
      @include r(767) {
        font-size: 50px;
        top: -70px; } }
    h2 {
      font-size: 35px;
      font-weight: normal;
      color: #12114a;
      margin-top: 120px;
      margin-bottom: 90px;
      @include r(767) {
        margin-bottom: 40px;
        margin-top: 70px; } }
    &__content {
      display: flex;
      flex-wrap: wrap;
      position: relative;
      @include r(1040) {
        justify-content: center; } }
    &__block {
      position: relative;
      min-height: 265px;
      border-radius: 10px;
      width: 100%;
      padding: 30px 22px 10px;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: flex-start;
      &:hover {
        .about__committee__block__info {
          opacity: 1;
          visibility: visible;
          transform: scale(1);
          h4, p, b {
            opacity: 1; } } }
      @include r(767) {
        margin-bottom: 20px {
          font-weight: normal; } }
      &-content {
        position: relative;
        margin: 0 10px;
        margin-bottom: 20px;
        width: 100%;
        max-width: 235px;
        @include r(500) {
          max-width: 100%;
          margin: 0px 0 20px; }
        &:before {
          content: '';
          position: absolute;
          background-image: url('../img/dot.png');
          @include cover;
          width: 180px;
          height: 101px;
          display: block;
          z-index: -1;
          @include r(767) {
            display: none; } }
        &-top {
          &:before {
            top: -30px;
            right: -30px; } }
        &-bottom {
          &:before {
            bottom: -30px;
            left: -30px; } } }
      img {
        width: 100%;
        height: 100%;
        @include object-fit(cover);
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        border-radius: 10px; }
      h4 {
        font-size: 18px;
        color: $white;
        text-shadow: 0px 0.5px 0.5px #000000; }
      p {
        font-size: 14px;
        font-weight: 600;
        color: $white;
        text-shadow: 0px 0.5px 0.5px #000000;
        margin-bottom: 10px; }
      b {
        font-size: 14px;
        font-weight: 600;
        color: $white; }
      &__info {
        position: absolute;
        width: calc(100% + 40px);
        height: calc(100% + 40px);
        top: -20px;
        bottom: -20px;
        left: -20px;
        right: -20px;
        padding: 20px 0;
        background-image: linear-gradient(159deg,#170472,#9c32ff 93%);
        opacity: 0;
        visibility: hidden;
        transform: scale(0.8);
        transition: all 0.3s;
        border-radius: 10px;
        z-index: 9;
        div {
          height: 100%;
          overflow-x: hidden;
          padding: 0px 20px;
          &::-webkit-scrollbar-track {
            background-color: linear-gradient(330deg, #8f2794, #3d30f3 99%); }
          &::-webkit-scrollbar-thumb {
            border-radius: 2px;
            background: #3d30f3; }
          &::-webkit-scrollbar {
            width: 7px;
            height: 7px; } }
        h4 {
          font-size: 28px;
          font-weight: normal;
          margin-bottom: 15px;
          opacity: 0;
          transition: all 0.2s 0.2s; }
        b {
          margin-bottom: 10px;
          display: block;
          text-transform: uppercase;
          opacity: 0;
          font-size: 14px;
          transition: all 0.2s 0.2s; }
        p {
          line-height: 1.6;
          opacity: 0;
          font-size: 12px;
          transition: all 0.2s 0.2s;
          margin-bottom: 10px; } } } }
  &__statement {
    min-height: 607px;
    height: 100%;
    background-color: #f8f8fa;
    @include center;
    position: relative;
    @include r(767) {
      min-height: 300px; }
    &__parallax {
      position: absolute;
      right: 8%;
      top: 0px;
      font-size: 175px;
      font-weight: bold;
      color: rgba(18, 17, 74, 0.1);
      text-transform: uppercase;
      z-index: 1;
      @include r(1040) {
        font-size: 120px; }
      @include r(767) {
        font-size: 50px;
        top: 0px; } }
    &__title {
      padding-top: 100px;
      @include r(767) {
        padding-top: 70px;
        padding-bottom: 20px; }
      h4 {
        font-size: 35px;
        font-weight: normal;
        text-align: center;
        color: #12114a;
        margin-bottom: 15px;
        @include r(767) {
          font-size: 22px; } }
      h2 {
        font-size: 50px;
        font-weight: bold;
        color: #12114a;
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 40px;
        @include r(1040) {
          font-size: 40px; }
        @include r(767) {
          font-size: 30px; } }
      p {
        font-size: 18px;
        font-weight: normal;
        line-height: 1.39;
        text-align: center;
        color: #12114a;
        max-width: 630px;
        position: relative;
        left: 50%;
        transform: translate(-50%); } } } }
