.aside {
  width: 70px;
  height: 100vh;
  box-shadow: 0px 1px 4px 0 rgba(0, 0, 0, 0.1);
  background-color: #010126;
  padding: 20px 0;
  position: fixed;
  left: 0px;
  transition: all 600ms ease;
  top: 0;
  z-index: 8;
  overflow-y: auto;
  overflow-x: hidden;
  @include r(767) {
    width: 45px; }
  &__content {
    width: 230px; }
  &.active {
    width: 230px;
    .aside__menu__item {
      color: rgba($pale-grey, 0.5);
      img {
        margin-right: 30px; } }
    li.is-active:before {
      left: calc(100% - 4px); } }
  img {
    width: 28px;
    @include r(767) {
      width: 22px; } }
  &__logo {
    padding-bottom: 40px;
    @include center;
    justify-content: flex-start;
    padding-left: 12px;
    transition: all 600ms ease;
    img {
      width: 45px; }
    @include r(767) {
      padding-right: 12px; } }
  &__menu {
    &__item {
      display: flex;
      align-items: center;
      font-size: 12px;
      font-weight: 600;
      color: transparent;
      transition: all 600ms ease;
      position: relative;
      img {
        margin-right: 0px;
        transition: all 600ms ease; } }
    li {
      position: relative;
      padding: 5px 20px;
      margin: 20px 0;
      @include tr;
      min-height: 35px;
      &.is-active {
        &:before {
          content: '';
          position: absolute;
          left: 66px;
          top: 0;
          display: block;
          width: 4px;
          height: 100%;
          background: #8b2e96;
          transition: all 600ms ease;
          @include r(767) {
            left: 41px; } } }
      @include r(767) {
        padding: 5px 10px;
        margin: 15px 0; }
      &:hover {
        background: rgba(#000, 0.2);
        .aside__menu__item {
          color: $white; }
        i {
          color: $white; } } } } }
