.pop-up {
  &-close {
    width: 30px;
    height: 30px;
    position: absolute;
    right: 20px;
    top: 20px;
    span {
      &:before, &:after {
        content: '';
        width: 30px;
        height: 2px;
        background: $black;
        position: absolute;
        top: 13px; }
      &:before {
        left: 0;
        transform: rotate(45deg); }
      &:after {
        right: 0;
        transform: rotate(-45deg); } } } }
.modal a.close-modal {
  display: none; }
.blocker {
  padding: 0;
  z-index: 99; }
.warning {
  max-width: 400px;
  border-radius: 10px;
  border: solid 0 #000000;
  background-color: $white;
  box-shadow: none;
  padding: 25px 30px;
  h3 {
    font-size: 18px;
    font-weight: bold;
    color: $charcoal-grey;
    margin-bottom: 10px; }
  p {
    font-size: 14px;
    font-weight: normal;
    color: $charcoal-grey;
    margin-bottom: 33px; }
  &__btn {
    @include center;
    justify-content: flex-end;
    a {
      font-weight: normal;
      &:first-child {
        border: solid 1px $charcoal-grey;
        color: $charcoal-grey;
        background: none;
        margin-right: 10px;
        &:before, &:after {
          display: none; }
        &:hover {
          background: $charcoal-grey;
          color: $white; } } } } }
